import * as React from 'react';
import type { HistoryPluginConfig } from './types';
import DefaultUndoButton, { UndoButtonProps } from './components/next/undoButton';
import DefaultRedoButton, { RedoButtonProps } from './components/next/redoButton';
import { ItemKeys, ItemSlot, RenderToolbar, SlotType } from '@ali/4ever-component';


export default function createRenderToolbar(
  historyConfig: HistoryPluginConfig,
): RenderToolbar {
  const locale = historyConfig.locale || {};

  const UndoButton = historyConfig.UndoButton || DefaultUndoButton;
  const RedoButton = historyConfig.RedoButton || DefaultRedoButton;

  const buttons: Partial<Record<ItemKeys, React.ComponentType<UndoButtonProps | RedoButtonProps>>> = {
    // @ts-ignore to fix
    'history.redo': RedoButton,
    // @ts-ignore to fix
    'history.undo': UndoButton,
  };


  return (props, controller) => {
    const { buttonName, extraProps } = props;

    const Component = buttons[buttonName] as React.ComponentType<UndoButtonProps | RedoButtonProps>;
    if (Component) {
      return (
        <ItemSlot display={SlotType.iconButton}>
          <Component
            controller={controller}
            locale={locale}
            {...extraProps}
          />
        </ItemSlot>
      );
    }

    return null;
  };
}
